	module.exports = {"data":{"markdownRemark":{"html":"<p>So after struggling with it all month, I finally shipped my Travelly course. It isn’t perfect but I’m pretty happy with it. Now that it’s shipped, let’s get down to specifics.</p>\n<h2>Context</h2>\n<p><a href=\"/blog/back-to-the-storyboard-throwing-it-all-out/\">As you saw in a previous post</a>, I completely revamped my entire challenge look. At first, I was going for something more underground-centric, but it didn’t seem to fit. So I decided to start afresh. I drew a lot of inspiration from AirBNB’s site and visuals. Context-wise, I wanted to ground the training firmly in  a sort of site back end, where a customer service agent might be able to look up rooms and research London. Design-wise, I went with something pretty clean and, at first, I only used couple of handwriting fonts. But after looking at it for a few days, I decided to add in a third font to make it more legible.</p>\n<h2>Challenge, Activity, and Feedback</h2>\n<p>Here I drew a ton of inspiration from the Allen Interactions Expedia demo. I went with three short challenges.</p>\n<h3>Challenge 1</h3>\n<p>The first one asks agents to really listen to the guest and help her to get the right room, use the right transportation, and identify two sightseeing and one shopping opportunity. They agent gets feedback after every activity to ask them to do a better job listening or  to reinforce their decisions.</p>\n<h3>Challenge 2</h3>\n<p>The second challenge differs a bit from the Allen Interactions model. I asked myself, what’s the biggest issue I have when I’m thinking about travelling to a new place? I’m brand new to a place and I may have a list of attractions I want to see, but the thing is, I have no idea of the proximity of anything to anything else, so I’m always wondering what exactly I’ll be next to or where’s the best place to book a stay. So this challenge asks agents to take in some vague hints from a customer about things he wants  to see and to recommend the best place to stay. In the case of a client course, I would likely want to punch this up a bit with several more opportunities. The other interesting thing about this that it also asks agents to remember the descriptions of the attractions they went over earlier. However, again, I thought to myself, while it would be good to be familiar with important London attractions, there probably isn’t any really reason to memorize them. So I took the opportunity to to practice my coding and I created a Google map using their JavaScript API. I created map pins using icons from the Noun Project. In this way, I could have an interactive map that would allow participants to poke around and see more information about the surroundings of each flat I created.</p>\n<h3>Challenge 3</h3>\n<p>The last challenge tries to put it all together, asking agents to get the right room and offer the right suggestions, but this time, with feedback withheld. In order to update the Allen Interactions model, I gave feedback in the form of Peeps (my non-copyrighted version of Tweets).</p>\n<h2>The Demo</h2>\n<p>So, after a month’s work of development, here’s my entry into the first GDS Challenge. Enjoy!</p>\n<figure>\n  <a href=\"http://knanthony.com/showcase/travelly/story.html\" target=\"blank\">\n    \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 810px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.30252100840336%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"Travelly learning game\" title=\"\" src=\"/static/travellyscreenshot21-e018df1bd03292cdd843fb8fb734e0aa-1626e.png\" srcset=\"/static/travellyscreenshot21-e018df1bd03292cdd843fb8fb734e0aa-076ec.png 203w,\n/static/travellyscreenshot21-e018df1bd03292cdd843fb8fb734e0aa-7ae34.png 405w,\n/static/travellyscreenshot21-e018df1bd03292cdd843fb8fb734e0aa-1626e.png 810w,\n/static/travellyscreenshot21-e018df1bd03292cdd843fb8fb734e0aa-a3d9c.png 1071w\" sizes=\"(max-width: 810px) 100vw, 810px\">\n    </span>\n  </span>\n  \n  </a>\n  <figcaption>Click on the image to view the demo</figcaption>\n</figure>","frontmatter":{"date":"May 25, 2015","path":"/blog/may-gds-challenge","title":"May GDS Challege: Call Center Training for Travelly","tags":["call center","training"],"categories":["Design","elearning","GDS Challenge"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Travelly-Shipped-5f0e87ab331e1ee6fed2d2a79021b3fc-81d39.png","srcSet":"/static/Travelly-Shipped-5f0e87ab331e1ee6fed2d2a79021b3fc-9a8d1.png 203w,\n/static/Travelly-Shipped-5f0e87ab331e1ee6fed2d2a79021b3fc-c53f9.png 405w,\n/static/Travelly-Shipped-5f0e87ab331e1ee6fed2d2a79021b3fc-81d39.png 810w,\n/static/Travelly-Shipped-5f0e87ab331e1ee6fed2d2a79021b3fc-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

